export default {
	data() {
		return {
			navlist :[
				{navname:'新订单'},
				{navname:'进行中'},
				{navname:'已完成'},
				{navname:'全部'}
			],/* 选中信息 */ 
			navActive:0,
			orderList:null,/*, 订单信息 */
			orderStatus:'正在刷新，请稍后'
		}
	}, 
	onLoad() {
		var userinfo = this.checkLogin();
		console.log(userinfo);
		this.getneworder(0);
	},
	onShow() {
		this.getneworder(0);
		uni.getLocation({
			type: 'wgs84',
			geocode:true,
			success: res => {
				console.log(res)
			},
			fail: () => {
				console.log('err')
			},
			complete: () => {}
		});
	},
	methods: {
		selectOrder:function(index){
			this.orderList = [];
			this.navActive = index;
			if (index==0) {
				this.getneworder(0)
			} else if(index==1){
				this.getneworder(1)//订单进行中
			} else if(index==2){
				this.getneworder(2)//订单已完成
			}else{
				this.getneworder('')//全部订单
			}
		},
		getneworder:function(status){
			uni.request({
				url: 'http://dongyi.sir6.cn/staff/order/list',
				method: 'GET',
				data: {
					st_id:uni.getStorageSync('STID'),
					status:status
				},
				success: res => {
					if(res.data.err==0){
						this.orderList = res.data.data;
					}else{
						this.orderList='';
						this.orderStatus='您没有订单信息';
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
